/**
 * Functions to initiate tools that exist on the page
 * at all times
 */import actionStack from'./actionStack.js';import grid from'./grid.js';export default{/**
   * Function to initiate the tool bar
   */init:function init(){this.eventListeners();},/**
   * Add the event-listener to global tools
   */eventListeners:function eventListeners(){var _this=this;// Undo button event-listener
$('#undo-button').on('click',function(){_this.undoClick();});// Redo button event-listener
$('#redo-button').on('click',function(){_this.redoClick();});// add keyboard shortcut for ctrl+z/undo
$(document).on('keydown',function(e){if(e.ctrlKey&&e.which===90){_this.undoClick();}});// add keyboard shortcut for ctrl+y/redo
$(document).on('keydown',function(e){if(e.ctrlKey&&e.which===89){_this.redoClick();}});$('#showhide-grid-button').on('click',function(){if(grid.visible){_this.changeGridVisibility(false);}else{_this.changeGridVisibility(true);}// toggle grid visibility
grid.visible=!grid.visible;});},/**
   * Function to run when we need to undo
   */undoClick:function undoClick(){var lastAction=actionStack.undoStack.pop();if(lastAction!==undefined){lastAction.undo();}},/**
   * Function to run when we need to redo
   */redoClick:function redoClick(){var lastAction=actionStack.redoStack.pop();if(lastAction!==undefined){lastAction.redo();}},/**
   * Change the grid visibility
   * @param {Boolean} visibility - True if grid to make it visible false otherwise
   */changeGridVisibility:function changeGridVisibility(visibility){var canvasGrid=grid.grid;// loop through and make all squares invisible
canvasGrid.forEach(function(rowElement){rowElement.forEach(function(gridSquare){gridSquare.square.path.visible=visibility;});});}};